.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  scroll-behavior: smooth;
}

#home,
#about,
#projects,
#skills,
#experience,
#education,
#contact {
  scroll-margin-top: 50px; /* Header height or any offset you prefer */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.navigation {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  flex-direction: row;
}

.navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-direction: row;
  display: flex;
}

.navigation li {
  margin-right: 10px;
  flex-direction: row;
}

.navigation button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  padding: 10px 20px;
  flex-direction: row;
}

.navigation button.active {
  font-weight: bold;
}

.sections {
  display: flex;
  flex-wrap: wrap;
}

.section {
  width: calc(33.33% - 20px);
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 20px;
}

.section h2 {
  margin-top: 0;
}
